'use strict';
/**
 * @api JUMP_ORDER({productCode,amount}) JUMP_ORDER
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {string} productCode 产品编号
 * @apiParam {number} amount 投资金额
 * @apiDescription APP 跳转标的投资订单页
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * // 跳转到投资订单页，显示购买 N7528 产品 金额10000
 * JUMP_ORDER({productCode:'N7528',amount:10000})
 * @apiParamExample {string} 返回值
 * 无
 **/

/**
 * todo JUMP
 */
import {WinJSDec, objError} from './APP/config';

var name = 'JUMP_ORDER';
var JUMP_ORDER = (obj) => {
  if (typeof obj !== 'object') {
    throw new TypeError(`[${name}] ${objError}`);
  }
  var {productCode, amount} = obj;
  WinJSDec(_ => {
    window.WinJSBridge.call('order', 'invest', {productCode, amount});
  });
};
export default JUMP_ORDER;
